.team-area{
    padding: 7.5rem 0 3rem;
    text-align: center;
}

.site-heading h6 {
display: block;
font-weight: 700;
margin-bottom: 10px;
text-transform: uppercase;
}

.site-heading h6 span {
color: #ff5a6e;
}

.site-heading h7 {
display: inline-block;
padding-bottom: 20px;
position: relative;
text-transform: capitalize;
z-index: 1;
}

.site-heading h7::before {
background: #ff5a6e none repeat scroll 0 0;
bottom: 0;
content: "";
height: 2px;
left: 50%;
margin-left: -25px;
position: absolute;
width: 50px;
}

.site-heading h2 span {
color: #ff5a6e;
}

.site-heading {
margin-bottom: 60px;
overflow: hidden;
margin-top: -5px;
text-align: center;
}

.team-area .single-item {
margin-bottom: 30px;
}

.team-area .item .thumb {
position: relative;
overflow: hidden;
z-index: 1;
}
